<template>
  <section v-if="works" id="works" class="works">
    <div class="container">
      <h2>Мои работы</h2>
      <div class="works__list">
        <work-example v-for="work in works"
        :key="work.id"
        :work="work"
      />
      </div>
    </div>
  </section>
</template>

<script>
import axios from 'axios';
import WorkExample from './WorkExample.vue';

export default {
  components: { WorkExample },
  name: 'WorksComponent',
  data() {
    return {
      works: '',
    };
  },
  async mounted() {
    try {
      const { data } = await axios.get('https://my-json-server.typicode.com/YuriyPonomarev224/projectsDB/projects');
      this.works = data;
    } catch (error) {
      throw new Error(error);
    }
  },
};
</script>

