<template>
  <a :href="work.link" class="work" target="_blank">
    <div class="work__image">
      <img :src="workImage" :alt=work.imageAlt>
    </div>
    <div class="work__title">
      {{workTitle}}
    </div>
  </a>
</template>

<script>
export default {
  name: 'WorkExampleComponent',
  props: {
    work: {
      type: Object,
      default() {
        return {
          title: 'lorem ipsum',
          imageSrc: './projects/images/imageSrc.png',
          imageAlt: 'temp alt',
          link: 'https://google.com',
        };
      },
    },
  },
  computed: {
    workImage() {
      if (this.work.imageSrc) {
        return this.work.imageSrc;
      }
      return './projects/images/imageSrc.png';
    },
    workTitle() {
      if (this.work.title) {
        return this.work.title;
      }
      return 'Lorem ipsum';
    },
  },
};
</script>

